import React, { Component } from 'react'
import Link from "gatsby-link";
import Bio from "./../bio/Bio";

class Header extends Component {

  render() {

    return (
      <header style={{
        marginBottom: `1rem`,
        marginTop: 0,
        position: 'fixed',
        left: '.25rem',
        top: '.5rem',
        zIndex: 1,
        display: `flex`
      }}>
       
          <Bio />

        <h1 style={{color: `#fff`, textShadow: `2px 2px #000`}}>
          {this.props.text}
        </h1>
      </header>
    )
  }
}

export default Header



